.bmix-service{
    &-wrap{
        padding: 30px 20px;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background: var(--light-color2);
        border: 1px solid var(--light-color2);
        a.bmix-icon-btn {
            font-size: 16px;
            line-height: 22px;
            font-weight: 700;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                width: 16px;
                height: 16px;
                transition: all 0.4s;
                margin: 0 0 5px 5px;
            }
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            border: 1px solid var(--white-color);
            background: var(--white-color);
            a.bmix-icon-btn{
                color: var(--accent-color);
                svg path{
                    fill: var(--accent-color);
                }
            }
        }
    }
    &-text{
        margin-bottom: 30px;
    }
    &-title-wrap{
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        justify-content: space-between;
    }
}
