.bmix-contact-info{
    &-wrapper{
        display: flex;
        align-items: center;
        gap: 100px;
        @include max (1199px){
            gap: 70px;
        }
        @include max (1199px){
            gap: 45px;
        }
        @include max (575px){
            display: block;
        }
    }
    &-icon{
        margin-bottom: 24px;
    }
    &-data{
        h5{
            color: var(--white-color);
            margin-bottom: 12px;
        }
        p{
            color: var(--light-color3);
        }
        ul{
            li{
                a{
                    color: var(--light-color3);
                }
            }
        }
    }
}
.mr-105{
    margin-right: 105px;
    @include max (1399px){
        margin-right: 50px;
    }
    @include max (1199px){
        margin-right: 0;
    }
    @include max (991px){
        margin-right: 105px;
    }
    @include max (767px){
        margin-right: 0;
    }
    @include max (575px){
        margin-top: 24px;
    }
}

.b-right5 {
    position: absolute;
    top: 22%;
    left: 38%;
    display: block;
    height: 120px;
    border-right: 1px solid var(--body-color);
    @include max (1399px){
        left: 41%;
    }
    @include max (1399px){
        left: 45%;
    }
    @include max (991px){
        left: 37%;
    }
    @include max (767px){
        display: none;
    }
}