
.bmix-accordion{
  &-item{
    position: relative;
    margin-bottom: 24px;
    transition: all 0.4s;
    &:last-child{
      margin-bottom: 0;
    }
    &.open{
      .bmix-accordion-body{
        display: block;
       p{
        font-size: 18px;
        line-height: 26px;
        letter-spacing: -0.001em;
        @include max (991px){
          text-align: left!important;
        }
       }
      }
    }
  }
  &-header{
    cursor: pointer;
    position: relative;
    display: flex;
    gap: 10px;
  }
  &-body{
    display: none;
    padding-top: 15px;
    padding: 0 90px 0 35px;
    &.default{
      display: block;
    }
    @include max(991px) {
      padding: 24px 0 0 35px;
    }
  }
  // End one
 
}

.bmix-accordion-wrapper {
display: flex;
gap: 24px;
@include max (991px){
    display: block;
}
}
.bmix-active-icon{
  transition: all .4s;
  display: flex;
  width: 26px;
  flex-shrink: 0;
  height: 26px;
  line-height: 31px;
  text-align: center;
  align-items: center;
justify-content: center;
  &.bmix-accordion-btn{
    img {
    width: 10px;
  }
}
}



.bmix-accordion-item.open {
  h5,p{
    color: var(--accent-color);
  }
  img.plasicon{
    opacity: 0!important;
  }
  img.mynusicon{
    opacity: 1;
    margin-right: 30px;
    margin-top: 2px;
    display: block;
  }
}

img.mynusicon {
  opacity: 0;
}
img.plasicon {
  opacity: 1;
}


