.bmix-p{
  &-wrap{
    &:hover{
      .bmix-p-content{
        opacity: 1;
        ::before{
          opacity: 1;
        }
      }
    }
    .bmix-p-content {
      position: absolute;
      transform: translate(-50%, -50%);
      top: 50%;
      left: 50%;
      opacity: 0;
      transition: all .4s;
      text-align: center;
      h4{
        color: var(--white-color);
        margin-bottom: 24px;
      }
    }
  }
  &-thumb{
    position: relative;
    display: block;
    margin-bottom: 24px;
    text-align: center;
    &::before{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      transition: .4s;
      background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
    }
    &:hover::before {
      opacity: .9;
    }
  }
}