.bmix-pricing{
  &-wrap{
    padding: 40px;
    border-radius: 10px;
    margin-bottom: 24px;
    border: 1px solid var(--light-color2);
    &.active{
      border: none;
      box-shadow: 0px 4px 80px 0px #00000014;
    }
  }
  &-header{
    margin-bottom: 30px;
    h4{
      margin-bottom: 20px;
    }
  }
  &-price{
    display: flex;
    margin-bottom: 30px;
    span{
      font-size: 24px;
      line-height: 32px;
      color: var(--heading-color);
    }
    p{
      display: flex;
      align-items: end;
    }
  }
  &-btn{
    margin-bottom: 30px;
  }
  &-body{
    ul{
      li{
        margin-bottom: 15px;
        position: relative;
        padding-left: 30px;
        &:last-child{
          margin-bottom: 0;
        }
        img{
          position: absolute;
          top: 5px;
          left: 0;
        }
      }
    }
  }
}