
.bmix-iconbox{
    &-wrap{
        display: flex;
        padding: 30px;
        align-items: center;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        justify-content: space-between;
        border: 1px solid var(--light-color2);

        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            background: var(--white-bg);
            svg path{
                fill: var(--accent-color);
            }
        }
    }
    &-content{
        display: flex;
        gap: 15px;
    }
    &-data{
        position: relative;
        h4{
            margin-top: 20px;
        }
    }
   &-title{
    .border-t{
        width: 45px;
        display: flex;
        margin-top: 5px;
        border-top: 1px solid var(--heading-color);
    }
    .number{
        position: absolute;
        top: -5px;
        left: 55px;
    }
   }
    // end service
   &-wrap2{
        padding: 30px 20px;
        border-radius: 10px;
        transition: all .4s;
        margin-bottom: 24px;
        border: 1px solid var(--light-color2);
        a.bmix-icon-btn{
            font-weight: 700;
            font-size: 16px;
            line-height: 22px;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                transition: all 0.4s;
            }
        }
        &:hover{
            border: 1px solid var(--accent-color);
            background: var(--accent-color);
            transform: rotate(-2deg);
            .bmix-iconbox-title-icon{
                svg path{
                    stroke: var(--white-color);
                    fill: var(--white-color);
                }
            }
            .bmix-iconbox-title-content{
                h5{
                    color: var(--white-color);
                }
            }
            .bmix-iconbox-text{
                p{
                    color: var(--white-color);
                }
            }
            a.bmix-icon-btn{
                color: var(--white-color);
                svg path{
                    fill: var(--white-color);
                }
            }
        }
   }
   &-title-wrap{
        display: flex;
        gap: 24px;
        align-items: center;
        margin-bottom: 15px;
   }
   &-title-content{
    h5{
        transition: all 0.4s;
    }
   }
   &-text{
        margin-bottom: 30px;
        p{
            transition: all 0.4s;
        }
    }
    
}
.bmix-service-site-btn{
    svg path{
        transition: all 0.4s
    }
}

// strat v2 process section

.process-section{
    h2{
        color: var(--white-color);
    }
}
.bmix-process{
    &-wrap{
        text-align: center;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .bmix-process-number h4{
                background: var(--white-bg);
            }
            
        }
    }
    &-number{
        display: flex;
        justify-content: center;
        h4{
            width: 80px;
            height: 80px;
            display: flex;
            align-items: center;
            border-radius: 100px;
            margin-bottom: 24px;
            justify-content: center;
            background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
        }
    }
    &-number2{
        ul{
            li{
                width: 60px;
                height: 60px;
                display: flex;
                align-items: center;
                border-radius: 100px;
                justify-content: center;
                background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
            }
        }
    }
    &-title{
        h4{
            color: var(--white-color);
            margin-bottom: 15px;
        }
        p{
            color: var(--white-color);
        }
    }
}