.bmix-teamv1{
    h2{
        color: var(--white-color);
    }
}
.bmix-team{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        background: var(--body-color);
    }
    &-thumb{
        padding-bottom: 20px;
        img{
            width: 100%;
        }
    }
    &-wraper{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-author{
        h5{
            color: var(--white-color);
            margin-bottom: 5px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
            color: var(--white-color);
        }
    }
}