.bmix-about{
    &-content{
        gap: 50px;
        display: flex;
        justify-content: space-between;
        @include max (1199px){
            gap: 20px;
        }
        @include max (767px){
            display: block;
        }
    }
    &-item{
        h4{
            margin-bottom: 10px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
        }
        @include max (767px){
            &:first-child{
                margin-bottom: 20px;
            }
        }
    }
    // end v1
    
    &-content-wrap{
        display: flex;
        align-items: center;
        border-radius: 10px;
        padding: 30px;
        gap: 35px;
        border: 1px solid var(--light-color2);
        margin-bottom: 15px;
        @include max (767px){
            justify-content: center;
        }
    }
    &-content-icon{
        flex-shrink: 0;
    }
    //  end v2
    &-content-wrap2{
        display: flex;
        gap: 50px;
        @include max (767px){
            display: block;
        }
    }
    &-content-title{
        @include max (767px){
            margin-top: 40px;
        }
        ul {
            li{
                position: relative;
                padding-left: 50px;
                h5{
                    margin-bottom: 10px;
                }
                img{
                    position: absolute;
                    top: 2px;
                    left: 0;
                }
                p{
                    font-size: 18px;
                    line-height: 26px;
                    letter-spacing: -0.001em;
                }
            }
        }
    }
}