.bmix-list-icon{
    &-content{
        ul{
            li{
                font-weight: 700;
                font-size: 24px;
                line-height: 32px;
                position: relative;
                letter-spacing: -0.005em;
                padding-left: 35px;
                margin-bottom: 15px;
                color: var(--heading-color);
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 5px;
                    left: 0;
                }
            }
        }
    }
}