
.bmix-features{
    &-content{
        padding: 30px;
        text-align: center;
        margin-bottom: 24px;
        border-radius: 5px;
        transition: all 0.4s;
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-icon{
        margin-bottom: 15px;
    }
    &-data{
        h4{
            margin-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
        }
    }
    
}
