
.bmix-section-padding{
    padding: 130px 0 56px;
    @include max(991px) {
        padding: 100px 0 56px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}
.bmix-section-padding2 {
    padding: 80px 0 130px;
    @include max(991px) {
        padding: 80px 0;
    }
}
.bmix-section-padding3 {
    padding: 100px 0;
    @include max(991px) {
        padding: 80px 0;
    }
}
.bmix-section-padding4 {
    padding: 130px 0 106px;
    @include max(991px) {
        padding: 100px 0 76px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
    @include max(425px) {
        padding: 60px 0 32px;
    }
}
.bmix-section-padding5 {
    padding: 80px 0 106px;
    @include max(991px) {
        padding: 80px 0 76px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}
.bmix-section-padding6 {
    padding: 130px 0;
    @include max(991px) {
        padding: 80px 0;
    }
}
.bmix-section-padding7 {
    padding: 80px 0;
}
.bmix-section-padding8 {
    padding: 50px 0 56px;
}
.bmix-section-padding9 {
    padding: 130px 0 170px;
    @include max (991px){
        padding: 100px 0 150px;
    }
    @include max (767px){
        padding: 80px 0;
    }
}
.bmix-section-padding9 {
    padding: 100px 0 80px;
    @include max (767px){
        padding: 80px 0;
    }
}
.bmix-section-padding10 {
    padding: 80px 0 56px;
}


.bmix-section-title {
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-620 {
            max-width: 620px;
        }
        &.max-width-630 {
            max-width: 630px;
        }
        &.max-width-700 {
            max-width: 700px;
        }
        &.max-width-750 {
            max-width: 750px;
        }
    }
}

