.bmix-title-btn {
    @include max(991px) {
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    
}

.bmix-extra-mt {
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
