.bmix-blog{
    &-wrap {
        border-radius: 10px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background: var(--light-color2);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            background: var(--white-color);
            .bmix-blog-img img {
                transform: scale(1.1);
            }
        }
        &:hover{
            .bmix-blog-btn-icon{
                color: var(--accent-color)!important;
            }
            svg path{
                stroke: var(--accent-color);
            }
        }
    }
    &-img{
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
        }
    }
    &-content{
        padding: 30px 24px;
        h5{
            margin-bottom: 10px;
        }
        p{
            color: var(--body-color);
        }
    }
  
    &-meta {
        margin-bottom: 24px;
        ul{
            li{
                display: inline-block;
                font-size: 16px;
                line-height: 22px;
                color: var(--heading-color);
                position: relative;
                padding-left: 30px;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    
    &-btn-icon{
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            margin-left: 10px;
        }
        
    }
