.mb-20{
    margin-bottom: 20px;
}

.bmix-post-navigation-wrap{
    display: flex;
    gap: 24px;
    max-width: 300px;
    .title-post-content{
        ul{
            li{
                font-size: 15px!important;
                line-height: 16px;
                font-weight: 400;
                margin-bottom: 10px;
            }
        }
    }
}
.nav-preview{
    &-wrap{
        @include max(991px){
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        
    }
    &-icon{
        margin-bottom: 24px;
        a{
            color: var(--dark-bg);
            font-size: 13px;
            font-weight: 700;
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}
.bmix-post-navigation-wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--light-color);
    margin-bottom: 40px;
}


.bmix-blog{
    &-thumb{
        img{
            width: 100%;
        }
    }
    &-thumb2{
        margin-bottom: 30px;
    }
}
.bmix-single-post-content-wrap{
    .bmix-blog-entry{
        &-content{
            p{
                font-size: 20px;
            }
            .bmix-blog-quote-content{
                &-wrap{
                    padding: 30px;
                    margin-top: 30px;
                    margin-bottom: 24px;
                    border-radius: 10px;
                    background: rgba(0, 34, 255, 0.05);
                }
                &-icon{
                    margin-bottom: 24px;
                }
                &-text{
                    margin-bottom: 24px;
                }
                &-authore{
                    h6{
                        font-weight: 600;
                        margin-bottom: 5px;
                    }
                }
                &-text2{
                    h6{
                        margin-bottom: 24px!important;
                    }
                    ul{
                        margin-top: 30px;
                        li{
                            font-weight: 600;
                            margin-bottom: 15px;
                            &:last-child{
                                margin-bottom: 0;
                            }
                        }
                    }
                    span{
                        font-weight: 400;
                        font-size: 20px;
                        line-height: 32px;
                        color: var(--body-color);
                    }
                }
            }
        }
    }
    .bmix-post-navigation{
        display: flex;
        align-items: center;
        padding-top: 24px;
        border-top: 1px solid rgba(0, 34, 255, 0.1);
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(0, 34, 255, 0.1);
        margin-bottom: 40px;
        justify-content: space-between;
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
        .bmix-nav-previous{
            a.nav-previous {
                display: flex;
                align-items: center;
                gap: 10px;
                margin-bottom: 18px;
                
                .bmix-previous-icon{
                    img{
                        margin-bottom: 5px;
                    }
                }
                
            }
            .nav-previous-content{
                p{
                    max-width: 320px;
                }
            }
            &.tae{
                text-align: end;
                a.jce {
                    justify-content: end;
                }
            }
        }
        
       
    }
}


.bmix-blog-tag{
    &-wrapper {
        display: flex;
        justify-content: space-between;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(0, 34, 255, 0.1);
        margin-bottom: 40px;
        @include max (991px){
            display: block;
        }
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    }
    &-wrap{
        display: flex;
        gap: 5px;
        
        @include max (991px){
            h5{
                margin-bottom: 20px;
            }
        }
        ul{
            li{
                margin-left: 10px;
                display: inline-block;
                margin-bottom: 15px;
                @include max (991px){
                    margin-left: 30px;
                }
            }
        }
    }
}

.bmix-post-comment{
    h5{
        margin-bottom: 24px;
    }
    ul{
        li{
            
            &.bmix-post-comment-wrap {
                display: flex;
                gap: 15px;
                &.children{
                    padding-top: 24px;
                    border-top: 1px solid rgba(0, 34, 255, 0.1);
                    margin-top: 24px;
                    padding-bottom: 24px;
                    border-bottom: 1px solid rgba(0, 34, 255, 0.1);
                    margin-bottom: 24px;
                    margin-left: 85px;
                    
                }
                .bmix-post-comment{
                    &-data{
                        position: relative;
                        max-width: 675px;
                        @include max (1199px){
                            max-width: 470px;
                        }
                        @include max (767px){
                            max-width: 330px;
                        }
                        .bmix-comment-reply{
                            position: absolute;
                            bottom: 85px;
                            right: -60px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                max-width: 470px;
                                bottom: 115px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 180px;
                                right: -65px;
                            }
                            @include max (425px){
                                right: 30px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    &-data2{
                        position: relative;
                        max-width: 593px;
                        @include max (1199px){
                            max-width: 418px;
                        }
                        @include max (767px){
                            max-width: 300px;
                        }
                        .bmix-comment-reply2{
                            position: absolute;
                            bottom: 85px;
                            right: -85px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                bottom: 120px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 150px;
                                right: -50px;
                            }
                            @include max (425px){
                                bottom: 180px;
                                right: 24px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    
                }
                h6{
                    margin-bottom: 5px;
                }
                p{
                    margin-top: 15px;
                }
            }
            &.bmix-post-comment-wrap2{
                padding-bottom: 50px;
                border-bottom: 1px solid rgba(0, 34, 255, 0.1);
                margin-bottom: 50px;
            }
        }
    }
}

