.bmix-t{
    &-wrap{
        padding: 24px;
        border-radius: 10px;
        margin-bottom: 24px;
        border: 1px solid var(--light-color2);
    }
    &-rating{
        ul{
            li{
                display: inline-block;
                margin-bottom: 28px;
            }
        }
    }
    &-data{
        margin-bottom: 30px;
        h5{
            margin-bottom: 10px;
        }
       
    }
    &-author{
        display: flex;
        gap: 10px;
        p{
            font-size: 16px;
            line-height: 22px;
            letter-spacing: -0.001em;
        }
    }
    // end v1
    &-wrap2{
        padding: 30px;
        border-radius: 10px;
        background: var(--white-bg);
    }
    &-thumb{
        margin-bottom: 15px;
    }
    &-data2{
        padding-bottom: 30px;
        border-bottom: 1px solid var(--light-color2);
        margin-bottom: 30px;
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-author2{
        h6{
            margin-bottom: 5px;
        }
        p{
            font-size: 16px;
            line-height: 22px;
            letter-spacing: -0.001em;
        }
    }
}
.bmix-t-slider-init{
    .slick-slide {
        margin: 0 12px;
    }
    .slick-dots button {
        display: block;
        padding: 0;
        border: none;
        text-indent: -9999px;
        transition: .3s;
        width: 23px;
        height: 6px;
        background: #060E281A;
        opacity: 1;
        border-radius: 50px;
        transition: all 0.4s;
        @include max (767px){
            display: none;
        }
    }
    ul.slick-dots {
        bottom: -22%;
        right: 45%;
        position: absolute;
        display: flex;
        gap: 24px;
        @include max (1199px){
            right: 40%;
            bottom: -15%;
        }
        @include max (767px){
            display: none;
        }
    }
    .slick-dots li.slick-active {
        background: var(--accent-color);
        border-radius: 50px;
        width: 45px;
        transition: all 0.4s;
        cursor: pointer;
        @include max (767px){
            display: none;
        }
    }
    ul.slick-dots li {
        transition: all .4s;
    }
}
